import React from 'react'

export default ({ hostname, tabStack, stackIndex, onStackGoIndex }) => {
  const navigationButtonsTop = 13;
  const websiteExtendedWidth = 350;
  const websiteReducedWidth = 230;


  let stackHasIndex = (index) => {
    return (index >= 0) && (index <= tabStack.length-1);
  };

  let stackCanGoBack = () => {
    return stackHasIndex(stackIndex + 1);
  };

  let stackCanGoForward = () => {
    return stackHasIndex(stackIndex - 1);
  };

  let stackTrackersBlockedAtIndex = (index) => {
    if (stackHasIndex(index)) {
      let tabInfo = tabStack[index];
      return tabInfo.trackersBlocked;
    }
    return 0;
  };

  let stackTrackersBlockedBack = () => {
    return stackTrackersBlockedAtIndex(stackIndex + 1);
  }

  let stackTrackersBlockedForward = () => {
    return stackTrackersBlockedAtIndex(stackIndex - 1);
  }

  let onStackGoBack = () => {
    onStackGoIndex(stackIndex + 1);
  }

  let onStackGoForward = () => {
    onStackGoIndex(stackIndex - 1);
  }

  let stackBackButton = '';
  if (stackCanGoBack()) {
    let backText = '';
    if (stackTrackersBlockedBack() > 0) {
      backText = <div className="backTrackers">{stackTrackersBlockedBack()}</div>;
    }
    stackBackButton =
      <label style={{float: 'left'}}>
        <button id="stackBack" className="back" style={{ top: navigationButtonsTop }} onClick={onStackGoBack} />
        {backText}
      </label>;
  }

  let stackForwardButton = '';
  if (stackCanGoForward()) {
    let forwardText = '';
    if (stackTrackersBlockedForward() > 0) {
      forwardText = <div className="forwardTrackers">{stackTrackersBlockedForward()}</div>
    }
    stackForwardButton =
    <label style={{float: 'right'}}>
      {forwardText}
      <button id="stackForward" className="forward" style={{ top: navigationButtonsTop }} onClick={onStackGoForward} />
    </label>;
  }

  let websiteMaxWidth = websiteExtendedWidth;
  if (stackCanGoBack() || stackCanGoForward()) {
    websiteMaxWidth = websiteReducedWidth;
  }

  return (
    <div className="website-navigation">
      {stackBackButton}
      <div className="website" title={hostname} style={{ maxWidth: websiteMaxWidth }}>
        {hostname}
      </div>
      {stackForwardButton}
    </div>
  )
}
