import React from 'react'
import ReactDOM from 'react-dom'
import Home from './ui/home'
import defaultState from './storage/defaults'
import './css/main.css'
import * as Sentry from '@sentry/browser';
import {
  replaceStringInObjectProperties,
} from './utils/helpers'

function sentryBeforeSend(event, hint)
{
  let regExp = new RegExp("[a-z]+-extension\:\/\/[^\/]+\/", 'gi' );
  event = replaceStringInObjectProperties(event, regExp, "extension://antitracker/")

  if (process.env.NODE_ENV === 'development') {
    const error = hint.originalException;
    if (error && error.message && error.message.match(/testIntentionalCrash/i)) {
      event.fingerprint = ['testIntentionalCrash'];
    }
  }

  return event;
}

if (process.env.NODE_ENV === 'development') {
  Sentry.init({
    dsn: 'https://06c0f0bebda545ffa4c0601a6aaff639@catch-nimbus.bitdefender.net/146',
    debug: true,
    environment: process.env.NODE_ENV,
    release: process.env.APP_RELEASE,
    beforeSend : sentryBeforeSend
  });
} else {
  Sentry.init({
    dsn: 'https://06c0f0bebda545ffa4c0601a6aaff639@catch-nimbus.bitdefender.net/146',
    environment: process.env.NODE_ENV,
    release: process.env.APP_RELEASE,
    beforeSend : sentryBeforeSend
  });
}

//Chrome imposes a limit of 800x600 for Extension popup windows
//https://ayastreb.me/chrome-extension-with-react/
//https://code.visualstudio.com/blogs/2016/02/23/introducing-chrome-debugger-for-vs-code
//https://developer.chrome.com/webstore/i18n

chrome.storage.local.get(storedState => {

  const initialState = { ...defaultState, ...storedState }

  chrome.tabs.query({ currentWindow: true, lastFocusedWindow: true, active: true }, tabs => {
    const [activeTab] = tabs;
    if (!activeTab) {
      return;
    }
    ReactDOM.render(
      <Home currentTab={activeTab} {...initialState} />,
      document.getElementById('root')
    )
  })
})
