import React from 'react'

export default ({ sessionWhitelist, currentUrl, onSiteDisable, onSiteEnable, bdLang }) => {

  let currentState = true;
  let currentCallback = onSiteDisable;
  if(undefined != sessionWhitelist && sessionWhitelist.includes(currentUrl)) {
    currentState = false;
    currentCallback = onSiteEnable;
  }
  return (
    <div>
      <label className="play-pause">
        <input type="checkbox" checked={currentState} onChange={currentCallback} />
        <div>
          <span>{bdLang.getString("pause_protection")}</span>
          <span>{bdLang.getString("enable_protection")}</span>
        </div>
      </label>
      <div className="enable-shadow">{bdLang.getString("enable_app")}</div>
    </div>
  )
}
