import React from 'react'
import { getTrackerCategoryName, getTrackerCategoryDescription, getTrackerCategoryStatus, SCREEN_UI } from '../utils/helpers'
import parseUrl from '../utils/parseUrl'
import Log from '../logger/log'



export default ({ currentScreen, currentCategoryKey, categoryExpandRef, categories, 
                  trackersMap, categoryStatusChanged, onClickCategoryLink, onClickBack, bdLang }) => {
  const displayTrackersUrls = () => {
    let listItems = [];

    if (undefined === trackersMap || undefined === trackersMap[currentCategoryKey] || null === trackersMap[currentCategoryKey] ) {
      return listItems;
    }

    function CatInfo(_url, _urlCount) {
      this.url = _url;
      this.urlCount = _urlCount;
    }

    let trackersUrlList = trackersMap[currentCategoryKey];
    let trackersCatMap = new Map();
    for (let index = 0; index < trackersUrlList.length; ++index) {
      let url = trackersUrlList[index];
      let hostName = parseUrl(url).hostname;
      if (trackersCatMap.has(hostName)) {
        let catInfo = trackersCatMap.get(hostName);
        catInfo.urlCount = catInfo.urlCount + 1;
        trackersCatMap.set(hostName, catInfo);
      }else{
        let catInfo = new CatInfo(url, 1);
        trackersCatMap.set(hostName, catInfo);
      }
    }

    let indexKey = 0;
    for (let [key, value] of trackersCatMap) {
      listItems.push(
        <li key={indexKey++}>
          <div className="name"><span>{key}</span><span className="count">{value.urlCount}</span></div>
          <div className="url" title={value.url}>{value.url}</div>
        </li>
      );
    }

    return listItems;
  }

  if (currentScreen & SCREEN_UI.CATEGORY) {
    Log.info('current screen: ' + currentScreen + ' current category key: ' + currentCategoryKey);
    let currentStatus = true;
    if (categories & currentCategoryKey) {
      currentStatus = false;
    }

    let categoryInfo = getTrackerCategoryName(bdLang, currentCategoryKey);
    let nrTrackers = 0;
    if ((undefined != trackersMap) && (undefined != trackersMap[currentCategoryKey]) && (null != trackersMap[currentCategoryKey])) {
      nrTrackers = trackersMap[currentCategoryKey].length;
    }
    let categoryTitle = categoryInfo.blocked.replace("%d", nrTrackers);
    let categoryButtonStatus = getTrackerCategoryStatus(bdLang, currentCategoryKey, currentStatus);
    let linkDescriptionDisplay = false;
    let catDescriptionText = getTrackerCategoryDescription(bdLang, currentCategoryKey);
    Log.info('desc size: ' + catDescriptionText.length);
    if(catDescriptionText.length < 118){
      linkDescriptionDisplay = true;
    }
    return (
      <div className="category-screen show">
        <div className="dashboard _expand" ref={categoryExpandRef}>
          <button className="back" onClick={onClickBack}></button>
          <img className={categoryInfo.icon} />
          <div className="category-title">{categoryTitle}</div>
          <div className="description">{catDescriptionText}</div>
          <div hidden={linkDescriptionDisplay}>
            <a onClick={onClickCategoryLink} >
              <span>{bdLang.getString("show_more_details")}</span>
              <span>{bdLang.getString("hide_more_details")}</span>
            </a>
          </div>
         
          <br />
          <label className="block-all">
            <input type="checkbox" checked={currentStatus} onChange={categoryStatusChanged} />
            <div>
              <span>{categoryButtonStatus}</span>
              <span>{categoryButtonStatus}</span>
            </div>
          </label>
        </div>
        <ul className={categoryInfo.className}>{displayTrackersUrls()}</ul>
      </div>
    )
  } else {
    return (null)
  }
}
