import React from 'react'
import { SCREEN_UI } from '../utils/helpers'

export default ({ currentScreen, extensionEnabled, enableSwitchWasChanged, extensionSpecialOffers, specialOffersSwitchWasChanged,
                  whitelist, inputWhitelistUrlRef, whitelistUrlErrorRef, onClickBack,
                  addUrlToWhitelist, deleteUrlFromWhitelist, bdLang }) => {


    let statusString = bdLang.getString("extension_status_on");
    if(!extensionEnabled){
        statusString = bdLang.getString("extension_status_off");
    }
    let specialOffersString = bdLang.getString("special_offers");
    let specialOffersTooltipString = bdLang.getString("special_offers_tooltip");
    const displayTrackersWhiteList = () => {

        let listItems = [];
        if (undefined === whitelist) {
            return listItems;
        }

        for (let i = 0; i < whitelist.length; ++i) {
            listItems.push(
                <li key={i}>
                    <div className="url">{whitelist[i]}</div>
                    <button className="remove" onClick={(e) => { deleteUrlFromWhitelist(e, whitelist[i]) }}></button>
                </li>
            );
        }
        return listItems;
    }

    if (currentScreen & SCREEN_UI.SETTINGS) {
        return (
            <div className="whitelist-screen show" >
                <div>
                    <button className="back" onClick={onClickBack}></button>
                    <div className="page-title">{bdLang.getString("settings_title")}</div>

                    <div className="switch-wrap">
                        <div>
                            <div className="lbl">{statusString}</div>
                            <div className="feat-description">{bdLang.getString("extension_status_desc")}</div>
                        </div>
                        <label className="switch">
                            <input type="checkbox" checked={extensionEnabled} onChange={enableSwitchWasChanged} />
                            <div></div>
                        </label>
                    </div>

                    <div className="switch-wrap sm">
                        <div>
                            <div className="lbl">{specialOffersString}</div>
                            <div className="tooltip-target">
                                <div className="info-tooltip">
                                    <div>{specialOffersTooltipString}</div>
                                </div>
                            </div>
                        </div>
                        <label className="switch">
                            <input id="start_stop_button" type="checkbox" checked={extensionSpecialOffers} onChange={specialOffersSwitchWasChanged}/>
                            <div></div>
                        </label>
                    </div>

                    <div className="section-title">{bdLang.getString("exceptions_title")}</div>
                    
                    <div className="page-description">{bdLang.getString("exceptions_desc")}</div>
                    <a onClick={(e) => { addUrlToWhitelist(e, true) }} >{bdLang.getString("add_curr_website")}</a><br />
                    <div className="added" hidden>{bdLang.getString("website_added_list")}</div>
                    <div className="field-label">{bdLang.getString("exception_action_desc")}</div>
                    <div className="field">
                        <div>
                            <input type="text" className="text-field" placeholder={bdLang.getString("website_url_enter")} ref={inputWhitelistUrlRef}></input>
                            <div className="error-text" ref={whitelistUrlErrorRef}>{bdLang.getString("url_not_valid")}</div>
                        </div>
                        <button className="add" onClick={(e) => { addUrlToWhitelist(e, false) }}></button>
                    </div>
                
                </div>

                <ul className="url-list">{displayTrackersWhiteList()}</ul>

                <div className="ad" hidden>
                    <div>Try the lastest version of</div>
                    <div className="product-name">Bitdefender Antivirus Plus</div>
                    <button className="get-free">Get 3 months free</button>
                </div>

            </div>
        )
    } else {
        return (null)
    }
}
